<template>
  <div>

    <div class="pagebar-title">新增种类</div>

    <div class="module">
      <div class="mtitle">种类名称</div>
      <el-input v-model="cateName" placeholder="种类名称"></el-input>
    </div>

    <div class="module">
      <el-button @click="push()">提交</el-button>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import { Addmainspecies } from "@/api/cropsdata";
export default {
  name: "Home",
  data() {
    return {
      cateName: null,
    };
  },
  components: {
  },
  methods: {
    push() {
      this.$http(Addmainspecies({
        name: this.cateName,
      }), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
  },
  created() {
  }
};
</script>
<style lang="scss" scoped>
</style>